.chat-widget .ask-btn {
  padding: 8px 12px;
  background: #0177ff;
  color: white;
  border-radius: 6px;
  border: none;
  cursor: pointer;
}

.chat-modal {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 360px;
  background: #071021;
  color: #fff;
  border-radius: 10px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  z-index: 9999;
}

.chat-header {
  padding: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(90deg, #0b1220, #122032);
}

.chat-messages {
  height: 300px;
  overflow-y: auto;
  padding: 10px;
}

.msg {
  margin-bottom: 10px;
  display: block;
}

.bot .bot-text {
  background: #0b2540;
  padding: 10px;
  border-radius: 8px;
  max-width: 92%;
}

.user .user-text {
  background: #20384f;
  padding: 10px;
  border-radius: 8px;
  text-align: right;
  max-width: 92%;
  margin-left: auto;
}

.btn-row {
  margin-top: 8px;
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.quick-btn {
  background: #1f6feb;
  border: none;
  color: white;
  padding: 6px 8px;
  border-radius: 6px;
  cursor: pointer;
}

.chat-input {
  padding: 10px;
  display: flex;
  gap: 8px;
  background: #071021;
  border-top: 1px solid #172033;
}

.chat-input input {
  flex: 1;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #233444;
  background: #071021;
  color: #fff;
  outline: none;
}

